.App {
  text-align: center;
}

.App-header,
.upload-section {
  background-color: #282c34;
  min-height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  border-bottom: 1px solid white;
}
.upload-input {
  display: flex;
  flex-direction: row;
}
.date-input {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.vendor-card-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  background-color: #282c34;
}
.vendor-card {
  font-size: 24px;
  color: white;
  border: 1px white solid;
  margin-top: 10px;
  padding-top: 5px;
  width: 270px;
  height: 190px;
}

.own {
  order: 3;
  border: 1px solid red;
}
